"use client";

import { motion, AnimatePresence } from "framer-motion";
import { useState, useRef, useEffect } from "react";
import { Mic, MicOff, Send } from "lucide-react";

interface VoiceButtonProps {
  /** Callback when recording starts */
  onRecordingStart?: () => void;
  /** Callback when recording stops with the transcript */
  onRecordingStop?: (transcript: string) => void;
  /** Callback when button is clicked (for non-voice submission) */
  onClick?: () => void;
  /** Button label */
  label?: string;
  /** Recording label */
  recordingLabel?: string;
  /** Button variant */
  variant?: "primary" | "secondary" | "ghost";
  /** Size in pixels */
  size?: number;
  /** Show transcript preview */
  showTranscript?: boolean;
  /** Custom className */
  className?: string;
}

export function VoiceButton({
  onRecordingStart,
  onRecordingStop,
  onClick,
  label = "Send Message",
  recordingLabel = "Listening...",
  variant = "primary",
  size = 48,
  showTranscript = true,
  className = "",
}: VoiceButtonProps) {
  const [isRecording, setIsRecording] = useState(false);
  const [transcript, setTranscript] = useState("");
  const [audioLevel, setAudioLevel] = useState(0);
  const mediaRecorderRef = useRef<MediaRecorder | null>(null);
  const animationRef = useRef<number | null>(null);
  const chunksRef = useRef<Blob[]>([]);

  // Simulate audio level for demo (replace with actual audio analysis in production)
  useEffect(() => {
    if (!isRecording) {
      setAudioLevel(0);
      return;
    }

    const updateLevel = () => {
      // Simulated audio level - replace with Web Audio API in production
      setAudioLevel(Math.random() * 0.5 + 0.1);
      animationRef.current = requestAnimationFrame(updateLevel);
    };

    animationRef.current = requestAnimationFrame(updateLevel);
    return () => {
      if (animationRef.current) {
        cancelAnimationFrame(animationRef.current);
      }
    };
  }, [isRecording]);

  const startRecording = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
      mediaRecorderRef.current = new MediaRecorder(stream);
      chunksRef.current = [];

      mediaRecorderRef.current.ondataavailable = (e) => {
        if (e.data.size > 0) {
          chunksRef.current.push(e.data);
        }
      };

      mediaRecorderRef.current.onstop = () => {
        stream.getTracks().forEach((track) => track.stop());
        // In production, send chunks to ElevenLabs Scribe API
        const transcriptText = "Voice input placeholder (integrate ElevenLabs Scribe API)";
        setTranscript(transcriptText);
        onRecordingStop?.(transcriptText);
      };

      mediaRecorderRef.current.start();
      setIsRecording(true);
      setTranscript("");
      onRecordingStart?.();
    } catch (error) {
      console.error("Failed to access microphone:", error);
    }
  };

  const stopRecording = () => {
    if (mediaRecorderRef.current && isRecording) {
      mediaRecorderRef.current.stop();
      setIsRecording(false);
    }
  };

  const handleClick = () => {
    if (isRecording) {
      stopRecording();
    } else {
      if (onClick) {
        onClick();
      } else {
        startRecording();
      }
    }
  };

  const variants = {
    primary: "bg-primary text-primary-foreground hover:bg-primary/90",
    secondary: "bg-secondary text-secondary-foreground hover:bg-secondary/80",
    ghost: "bg-transparent hover:bg-muted text-foreground",
  };

  return (
    <div className={`relative ${className}`}>
      <motion.button
        onClick={handleClick}
        className={`
          relative flex items-center justify-center rounded-full
          transition-colors focus:outline-none focus:ring-2 focus:ring-primary/50
          ${variants[variant]}
        `}
        style={{ width: size, height: size }}
        whileHover={{ scale: 1.05 }}
        whileTap={{ scale: 0.95 }}
      >
        {/* Audio level indicator ring */}
        <AnimatePresence>
          {isRecording && (
            <motion.div
              initial={{ scale: 1.5, opacity: 0.5 }}
              animate={{ scale: 1.5 + audioLevel * 0.5, opacity: 0.2 + audioLevel * 0.3 }}
              exit={{ scale: 1, opacity: 0 }}
              transition={{ duration: 0.1 }}
              className="absolute inset-0 rounded-full border-2 border-current"
              style={{ color: "var(--primary)" }}
            />
          )}
        </AnimatePresence>

        {/* Inner pulse */}
        <AnimatePresence>
          {isRecording && (
            <motion.div
              initial={{ scale: 1, opacity: 0.8 }}
              animate={{ scale: 1 + audioLevel * 0.3, opacity: 0.3 }}
              exit={{ scale: 1, opacity: 0 }}
              transition={{ duration: 0.1 }}
              className="absolute inset-2 rounded-full bg-current"
              style={{ color: "var(--primary)" }}
            />
          )}
        </AnimatePresence>

        {/* Icon */}
        <AnimatePresence mode="wait">
          {isRecording ? (
            <motion.div
              key="recording"
              initial={{ scale: 0 }}
              animate={{ scale: 1 }}
              exit={{ scale: 0 }}
            >
              <Mic className="w-5 h-5" />
            </motion.div>
          ) : transcript ? (
            <motion.div
              key="transcript-ready"
              initial={{ scale: 0 }}
              animate={{ scale: 1 }}
              exit={{ scale: 0 }}
            >
              <Send className="w-5 h-5" />
            </motion.div>
          ) : (
            <motion.div
              key="idle"
              initial={{ scale: 0 }}
              animate={{ scale: 1 }}
              exit={{ scale: 0 }}
            >
              <Mic className="w-5 h-5" />
            </motion.div>
          )}
        </AnimatePresence>
      </motion.button>

      {/* Transcript preview */}
      {showTranscript && transcript && (
        <motion.div
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          className="absolute top-full left-0 mt-2 p-3 bg-card rounded-lg border border-border shadow-lg max-w-xs"
        >
          <p className="text-sm text-card-foreground">{transcript}</p>
          <button
            onClick={() => {
              setTranscript("");
              setAudioLevel(0);
            }}
            className="mt-2 text-xs text-muted-foreground hover:text-foreground flex items-center gap-1"
          >
            <MicOff className="w-3 h-3" />
            Clear
          </button>
        </motion.div>
      )}

      {/* Recording indicator */}
      {isRecording && (
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          className="absolute -bottom-8 left-1/2 -translate-x-1/2 whitespace-nowrap"
        >
          <span className="text-xs flex items-center gap-1">
            <motion.span
              animate={{ opacity: [0.3, 1, 0.3] }}
              transition={{ duration: 1, repeat: Infinity }}
              className="w-2 h-2 rounded-full bg-red-500"
            />
            {recordingLabel}
          </span>
        </motion.div>
      )}
    </div>
  );
}

/**
 * Voice Input Button with label for forms
 */
export function VoiceInputButton({
  onTranscript,
  className = "",
}: {
  onTranscript: (text: string) => void;
  className?: string;
}) {
  return (
    <VoiceButton
      onRecordingStop={onTranscript}
      variant="ghost"
      size={40}
      className={className}
    />
  );
}
