"use client";

import { motion, AnimatePresence } from "framer-motion";
import { useState, useRef, useEffect } from "react";
import { Play, Pause, Volume2, VolumeX, Maximize2 } from "lucide-react";
import { Waveform } from "./Waveform";
import { ScrubBar } from "./Waveform";

interface AudioPlayerProps {
  /** Audio source URL */
  src: string;
  /** Audio title */
  title?: string;
  /** Artist/author name */
  artist?: string;
  /** Cover image URL */
  coverImage?: string;
  /** Auto-play on mount */
  autoPlay?: boolean;
  /** Callback when playback ends */
  onEnded?: () => void;
  /** Custom className */
  className?: string;
}

export function AudioPlayer({
  src,
  title,
  artist,
  coverImage,
  autoPlay = false,
  onEnded,
  className = "",
}: AudioPlayerProps) {
  const audioRef = useRef<HTMLAudioElement>(null);
  const [isPlaying, setIsPlaying] = useState(false);
  const [currentTime, setCurrentTime] = useState(0);
  const [duration, setDuration] = useState(0);
  const [volume, setVolume] = useState(1);
  const [isMuted, setIsMuted] = useState(false);
  const [audioLevel, setAudioLevel] = useState(0);

  useEffect(() => {
    const audio = audioRef.current;
    if (!audio) return;

    const updateTime = () => setCurrentTime(audio.currentTime);
    const updateDuration = () => setDuration(audio.duration);
    const handleEnded = () => {
      setIsPlaying(false);
      onEnded?.();
    };

    audio.addEventListener("timeupdate", updateTime);
    audio.addEventListener("loadedmetadata", updateDuration);
    audio.addEventListener("ended", handleEnded);

    return () => {
      audio.removeEventListener("timeupdate", updateTime);
      audio.removeEventListener("loadedmetadata", updateDuration);
      audio.removeEventListener("ended", handleEnded);
    };
  }, [onEnded]);

  // Simulate audio level visualization
  useEffect(() => {
    if (!isPlaying) {
      setAudioLevel(0);
      return;
    }

    const interval = setInterval(() => {
      setAudioLevel(Math.random() * 0.6 + 0.2);
    }, 100);

    return () => clearInterval(interval);
  }, [isPlaying]);

  const togglePlay = () => {
    const audio = audioRef.current;
    if (!audio) return;

    if (isPlaying) {
      audio.pause();
    } else {
      audio.play();
    }
    setIsPlaying(!isPlaying);
  };

  const handleSeek = (time: number) => {
    if (audioRef.current) {
      audioRef.current.currentTime = time;
      setCurrentTime(time);
    }
  };

  const handleVolumeChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = parseFloat(e.target.value);
    setVolume(value);
    if (audioRef.current) {
      audioRef.current.volume = value;
    }
    setIsMuted(value === 0);
  };

  const toggleMute = () => {
    if (audioRef.current) {
      audioRef.current.muted = !isMuted;
      setIsMuted(!isMuted);
    }
  };

  const formatTime = (seconds: number) => {
    const mins = Math.floor(seconds / 60);
    const secs = Math.floor(seconds % 60);
    return `${mins}:${secs.toString().padStart(2, "0")}`;
  };

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      className={`bg-card rounded-2xl border border-card-border overflow-hidden ${className}`}
    >
      <audio ref={audioRef} src={src} autoPlay={autoPlay} preload="metadata" />

      <div className="flex gap-4 p-4">
        {/* Cover art */}
        {coverImage && (
          <div className="relative w-20 h-20 rounded-xl overflow-hidden flex-shrink-0">
            <img
              src={coverImage}
              alt={title || "Audio cover"}
              className="w-full h-full object-cover"
            />
          </div>
        )}

        {/* Content */}
        <div className="flex-1 min-w-0">
          {/* Title and artist */}
          <div className="mb-3">
            {title && (
              <h3 className="font-semibold text-foreground truncate">{title}</h3>
            )}
            {artist && (
              <p className="text-sm text-muted-foreground truncate">{artist}</p>
            )}
          </div>

          {/* Waveform visualization */}
          <div className="mb-3">
            <Waveform
              barCount={50}
              barWidth={2}
              barGap={2}
              barRadius={1}
              height={40}
              isActive={isPlaying}
              audioLevel={audioLevel}
              progress={duration > 0 ? currentTime / duration : 0}
            />
          </div>

          {/* Controls */}
          <div className="flex items-center justify-between">
            {/* Play/Pause */}
            <motion.button
              onClick={togglePlay}
              className="w-10 h-10 rounded-full bg-primary flex items-center justify-center text-primary-foreground"
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              <AnimatePresence mode="wait">
                {isPlaying ? (
                  <motion.div
                    key="pause"
                    initial={{ scale: 0 }}
                    animate={{ scale: 1 }}
                    exit={{ scale: 0 }}
                  >
                    <Pause className="w-5 h-5" />
                  </motion.div>
                ) : (
                  <motion.div
                    key="play"
                    initial={{ scale: 0 }}
                    animate={{ scale: 1 }}
                    exit={{ scale: 0 }}
                  >
                    <Play className="w-5 h-5" />
                  </motion.div>
                )}
              </AnimatePresence>
            </motion.button>

            {/* Time and scrub */}
            <div className="flex-1 mx-4">
              <ScrubBar
                duration={duration}
                currentTime={currentTime}
                onSeek={handleSeek}
              />
            </div>

            {/* Volume */}
            <div className="flex items-center gap-2">
              <button
                onClick={toggleMute}
                className="p-2 hover:bg-muted rounded-lg transition-colors"
              >
                {isMuted ? (
                  <VolumeX className="w-4 h-4 text-muted-foreground" />
                ) : (
                  <Volume2 className="w-4 h-4 text-muted-foreground" />
                )}
              </button>
              <input
                type="range"
                min="0"
                max="1"
                step="0.1"
                value={isMuted ? 0 : volume}
                onChange={handleVolumeChange}
                className="w-16 h-1 accent-primary"
              />
            </div>
          </div>
        </div>
      </div>
    </motion.div>
  );
}
