"use client";

import { motion, AnimatePresence } from "framer-motion";
import { useState, useRef, useEffect } from "react";

interface MessageProps {
  /** Message content */
  content: string;
  /** Message role - user or assistant */
  role: "user" | "assistant";
  /** Avatar URL or component */
  avatar?: React.ReactNode;
  /** Timestamp */
  timestamp?: string;
  /** Whether message is loading/streaming */
  isLoading?: boolean;
  /** Custom className */
  className?: string;
}

export function Message({
  content,
  role,
  avatar,
  timestamp,
  isLoading = false,
  className = "",
}: MessageProps) {
  const isUser = role === "user";

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: -10 }}
      className={`flex gap-4 ${isUser ? "flex-row-reverse" : ""} ${className}`}
    >
      {/* Avatar */}
      <div className="flex-shrink-0">
        {avatar ? (
          <div className="w-10 h-10 rounded-full overflow-hidden">{avatar}</div>
        ) : (
          <div
            className={`w-10 h-10 rounded-full flex items-center justify-center ${
              isUser
                ? "bg-primary text-primary-foreground"
                : "bg-primary-lighter text-primary"
            }`}
          >
            {isUser ? "U" : "AI"}
          </div>
        )}
      </div>

      {/* Message bubble */}
      <div
        className={`max-w-[70%] rounded-2xl px-4 py-3 ${
          isUser
            ? "bg-primary text-primary-foreground"
            : "bg-muted text-foreground"
        }`}
      >
        {isLoading ? (
          <div className="flex gap-1">
            <motion.span
              className="w-2 h-2 rounded-full bg-current opacity-50"
              animate={{ scale: [1, 1.5, 1] }}
              transition={{ duration: 0.8, repeat: Infinity }}
            />
            <motion.span
              className="w-2 h-2 rounded-full bg-current opacity-50"
              animate={{ scale: [1, 1.5, 1] }}
              transition={{ duration: 0.8, repeat: Infinity, delay: 0.2 }}
            />
            <motion.span
              className="w-2 h-2 rounded-full bg-current opacity-50"
              animate={{ scale: [1, 1.5, 1] }}
              transition={{ duration: 0.8, repeat: Infinity, delay: 0.4 }}
            />
          </div>
        ) : (
          <p className="text-sm leading-relaxed whitespace-pre-wrap">{content}</p>
        )}

        {timestamp && (
          <p className={`text-xs mt-1 opacity-60`}>{timestamp}</p>
        )}
      </div>
    </motion.div>
  );
}

interface ConversationProps {
  /** Array of messages */
  messages: Array<{
    id: string;
    content: string;
    role: "user" | "assistant";
    timestamp?: string;
  }>;
  /** Currently loading message ID */
  loadingId?: string;
  /** Scroll to bottom on new messages */
  autoScroll?: boolean;
  /** Custom className */
  className?: string;
}

export function Conversation({
  messages,
  loadingId,
  autoScroll = true,
  className = "",
}: ConversationProps) {
  const bottomRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (autoScroll && bottomRef.current) {
      bottomRef.current.scrollIntoView({ behavior: "smooth" });
    }
  }, [messages, autoScroll]);

  return (
    <div className={`flex flex-col gap-4 ${className}`}>
      <AnimatePresence initial={false} mode="popLayout">
        {messages.map((message) => (
          <Message
            key={message.id}
            content={message.content}
            role={message.role}
            timestamp={message.timestamp}
            isLoading={loadingId === message.id}
          />
        ))}
      </AnimatePresence>
      <div ref={bottomRef} />
    </div>
  );
}

/**
 * Conversation container with sticky bottom behavior
 */
export function ConversationContainer({
  children,
  className = "",
}: {
  children: React.ReactNode;
  className?: string;
}) {
  return (
    <div
      className={`flex flex-col h-full overflow-y-auto p-4 space-y-4 ${className}`}
      style={{
        scrollBehavior: "smooth",
      }}
    >
      {children}
    </div>
  );
}
