"use client";

import { motion, useSpring, useTransform, useMotionValue } from "framer-motion";
import { useEffect, useRef, useState } from "react";

interface WaveformProps {
  /** Number of bars in the waveform */
  barCount?: number;
  /** Bar width in pixels */
  barWidth?: number;
  /** Gap between bars in pixels */
  barGap?: number;
  /** Bar border radius in pixels */
  barRadius?: number;
  /** Bar color */
  barColor?: string;
  /** Active bar color (during playback/recording) */
  activeColor?: string;
  /** Height of the waveform in pixels */
  height?: number;
  /** Whether the waveform is currently active */
  isActive?: boolean;
  /** Audio level for reactivity (0-1) */
  audioLevel?: number;
  /** Current playback progress (0-1) */
  progress?: number;
  /** Animation speed multiplier */
  speed?: number;
  /** Custom className */
  className?: string;
}

export function Waveform({
  barCount = 60,
  barWidth = 3,
  barGap = 2,
  barRadius = 2,
  barColor = "var(--muted-foreground, #6b7280)",
  activeColor = "var(--primary, #232323)",
  height = 60,
  isActive = false,
  audioLevel = 0,
  progress = 0,
  speed = 1,
  className = "",
}: WaveformProps) {
  const containerRef = useRef<HTMLDivElement>(null);
  const [bars, setBars] = useState<number[]>([]);
  const [mouseX, setMouseX] = useState<number | null>(null);

  // Generate random heights for bars
  useEffect(() => {
    const heights = Array.from({ length: barCount }, () => Math.random() * 0.6 + 0.2);
    setBars(heights);
  }, [barCount]);

  const handleMouseMove = (e: React.MouseEvent) => {
    if (containerRef.current) {
      const rect = containerRef.current.getBoundingClientRect();
      setMouseX((e.clientX - rect.left) / rect.width);
    }
  };

  const handleMouseLeave = () => {
    setMouseX(null);
  };

  return (
    <div
      ref={containerRef}
      className={`relative flex items-center justify-center gap-[${barGap}px] ${className}`}
      style={{ height }}
      onMouseMove={handleMouseMove}
      onMouseLeave={handleMouseLeave}
    >
      {bars.map((baseHeight, index) => {
        const position = index / barCount;
        const isPast = progress > position;
        const isNearMouse = mouseX !== null && Math.abs(position - mouseX) < 0.1;

        // Calculate bar height with reactivity
        let barHeight = baseHeight;
        if (isActive && audioLevel > 0) {
          barHeight = baseHeight + audioLevel * (Math.random() * 0.4 + 0.2);
        }
        if (isNearMouse) {
          barHeight *= 1.5;
        }

        return (
          <motion.div
            key={index}
            className="rounded-full"
            style={{
              width: barWidth,
              height: `${barHeight * height}px`,
              backgroundColor: isPast || isActive ? activeColor : barColor,
              borderRadius: barRadius,
            }}
            animate={{
              height: [
                `${barHeight * height}px`,
                `${(barHeight + (isActive ? audioLevel * 0.3 : 0)) * height}px`,
                `${barHeight * height}px`,
              ],
            }}
            transition={{
              duration: 0.1 / speed,
              repeat: isActive ? Infinity : 0,
              ease: "easeInOut",
            }}
          />
        );
      })}
    </div>
  );
}

/**
 * Live waveform for real-time audio visualization
 */
export function LiveWaveform({
  height = 80,
  barCount = 40,
  color = "var(--primary, #232323)",
  className = "",
}: {
  height?: number;
  barCount?: number;
  color?: string;
  className?: string;
}) {
  const [audioLevel, setAudioLevel] = useState(0);
  const animationRef = useRef<number | null>(null);

  useEffect(() => {
    // Simulated audio level - replace with Web Audio API
    const updateLevel = () => {
      setAudioLevel(Math.random() * 0.8 + 0.1);
      animationRef.current = requestAnimationFrame(updateLevel);
    };

    animationRef.current = requestAnimationFrame(updateLevel);
    return () => {
      if (animationRef.current) {
        cancelAnimationFrame(animationRef.current);
      }
    };
  }, []);

  return (
    <div className={`flex items-center justify-center ${className}`}>
      <Waveform
        barCount={barCount}
        height={height}
        barWidth={4}
        barGap={3}
        barRadius={4}
        activeColor={color}
        isActive={true}
        audioLevel={audioLevel}
        speed={2}
      />
    </div>
  );
}

/**
 * Audio scrub bar with draggable progress
 */
export function ScrubBar({
  duration = 60,
  currentTime = 0,
  onSeek,
  color = "var(--primary, #232323)",
  className = "",
}: {
  duration?: number;
  currentTime?: number;
  onSeek?: (time: number) => void;
  color?: string;
  className?: string;
}) {
  const progress = duration > 0 ? currentTime / duration : 0;
  const [isDragging, setIsDragging] = useState(false);

  const formatTime = (seconds: number) => {
    const mins = Math.floor(seconds / 60);
    const secs = Math.floor(seconds % 60);
    return `${mins}:${secs.toString().padStart(2, "0")}`;
  };

  return (
    <div className={`w-full space-y-2 ${className}`}>
      {/* Progress bar */}
      <div
        className="relative h-2 bg-muted rounded-full cursor-pointer overflow-hidden"
        onClick={(e) => {
          if (onSeek) {
            const rect = e.currentTarget.getBoundingClientRect();
            const percent = (e.clientX - rect.left) / rect.width;
            onSeek(percent * duration);
          }
        }}
      >
        {/* Progress fill */}
        <motion.div
          className="absolute left-0 top-0 h-full rounded-full"
          style={{ backgroundColor: color }}
          initial={false}
          animate={{ width: `${progress * 100}%` }}
        />

        {/* Scrubber handle */}
        <motion.div
          className="absolute top-1/2 -translate-y-1/2 w-4 h-4 rounded-full bg-white shadow-md"
          style={{ left: `calc(${progress * 100}% - 8px)` }}
          animate={{ scale: isDragging ? 1.2 : 1 }}
        />
      </div>

      {/* Time display */}
      <div className="flex justify-between text-xs text-muted-foreground">
        <span>{formatTime(currentTime)}</span>
        <span>{formatTime(duration)}</span>
      </div>
    </div>
  );
}
