"use client";

import { motion, useScroll, useTransform } from "framer-motion";
import { useRef } from "react";
import { cn } from "@/lib/utils";

interface ShimmeringTextProps {
  /** Text content */
  children: React.ReactNode;
  /** Tag to render */
  as?: "h1" | "h2" | "h3" | "h4" | "p" | "span";
  /** Text color */
  color?: string;
  /** Shimmer gradient colors */
  shimmerColors?: string[];
  /** Animation duration in seconds */
  duration?: number;
  /** Viewport trigger */
  once?: boolean;
  /** Custom className */
  className?: string;
}

export function ShimmeringText({
  children,
  as: Component = "span",
  color = "var(--foreground, #232323)",
  shimmerColors = [
    "var(--foreground, #232323)",
    "var(--primary, #3b82f6)",
    "var(--primary-lighter, #60a5fa)",
    "var(--foreground, #232323)",
  ],
  duration = 3,
  once = true,
  className = "",
}: ShimmeringTextProps) {
  const containerRef = useRef<HTMLDivElement>(null);
  const { scrollYProgress } = useScroll({
    target: containerRef,
    offset: ["start end", "end start"],
  });

  const x = useTransform(scrollYProgress, [0, 0.5, 1], ["-100%", "100%", "-100%"]);
  const opacity = useTransform(scrollYProgress, [0, 0.3, 0.7, 1], [0.3, 1, 1, 0.3]);

  const textGradient = {
    backgroundImage: `linear-gradient(90deg, ${shimmerColors.join(", ")})`,
    backgroundSize: "300% 100%",
    WebkitBackgroundClip: "text",
    WebkitTextFillColor: "transparent",
    backgroundClip: "text",
  };

  const MotionComponent = motion.create(Component as any);

  return (
    <div ref={containerRef} className="relative inline-block">
      {/* Base text */}
      <Component className={cn("relative z-10", className)} style={{ color }}>
        {children}
      </Component>

      {/* Shimmer overlay */}
      <MotionComponent
        className="absolute inset-0 z-20 pointer-events-none"
        style={{
          ...textGradient,
          opacity: 0,
          x,
        }}
        initial={{ opacity: 0 }}
        whileInView={{ opacity: 1 }}
        viewport={{ once, margin: "-10%" }}
        animate={{
          backgroundPosition: ["0% 0%", "100% 0%", "0% 0%"],
        }}
        transition={{
          duration,
          repeat: Infinity,
          ease: "linear",
        }}
      >
        {children}
      </MotionComponent>
    </div>
  );
}

/**
 * Shimmering Heading with preset styles
 */
export function ShimmeringHeading({
  children,
  level = 2,
  className = "",
}: {
  children: React.ReactNode;
  level?: 1 | 2 | 3 | 4;
  className?: string;
}) {
  const sizes = {
    1: "text-4xl md:text-5xl lg:text-6xl",
    2: "text-3xl md:text-4xl",
    3: "text-2xl md:text-3xl",
    4: "text-xl md:text-2xl",
  };

  const tags = { 1: "h1", 2: "h2", 3: "h3", 4: "h4" } as const;

  return (
    <ShimmeringText
      as={tags[level]}
      className={`${sizes[level]} font-bold ${className}`}
      shimmerColors={[
        "var(--foreground)",
        "var(--primary)",
        "var(--primary-lighter)",
        "var(--foreground)",
      ]}
    >
      {children}
    </ShimmeringText>
  );
}
