"use client";

import { motion, useSpring, useTransform, MotionValue } from "framer-motion";
import { useRef } from "react";

interface OrbProps {
  /** Width and height of the orb in pixels */
  size?: number;
  /** Primary color (hex or CSS variable) */
  color?: string;
  /** Secondary color for gradient */
  secondaryColor?: string;
  /** Speed of the pulsing animation */
  speed?: number;
  /** Whether the orb reacts to audio */
  audioReactive?: boolean;
  /** Current audio level for reactivity (0-1) */
  audioLevel?: number;
  /** Custom className */
  className?: string;
}

export function Orb({
  size = 200,
  color = "var(--primary, #232323)",
  secondaryColor = "var(--primary-lighter, #4a4a4a)",
  speed = 1,
  audioReactive = false,
  audioLevel = 0,
  className = "",
}: OrbProps) {
  const containerRef = useRef<HTMLDivElement>(null);
  const mouseX = useRef(0);
  const mouseY = useRef(0);

  const springConfig = { damping: 20, stiffness: 200, mass: 1 };
  const x = useSpring(0, springConfig);
  const y = useSpring(0, springConfig);
  const scale = useSpring(1, { damping: 15, stiffness: 300, mass: 1 });

  const handleMouseMove = (e: React.MouseEvent) => {
    if (!containerRef.current) return;
    const rect = containerRef.current.getBoundingClientRect();
    mouseX.current = e.clientX - rect.left - rect.width / 2;
    mouseY.current = e.clientY - rect.top - rect.height / 2;
    x.set(mouseX.current * 0.1);
    y.set(mouseY.current * 0.1);
  };

  const handleMouseLeave = () => {
    x.set(0);
    y.set(0);
  };

  // Audio reactive scale transform
  const audioScale = useTransform(scale, (s) =>
    audioReactive ? s + audioLevel * 0.2 : s
  );

  return (
    <div
      ref={containerRef}
      className={`relative flex items-center justify-center ${className}`}
      style={{ width: size, height: size }}
      onMouseMove={handleMouseMove}
      onMouseLeave={handleMouseLeave}
    >
      <motion.div
        style={{
          width: size,
          height: size,
          x,
          y,
          scale: audioScale,
        }}
        animate={{
          scale: [1, 1 + 0.05 * speed, 1],
        }}
        transition={{
          duration: 3 / speed,
          repeat: Infinity,
          ease: "easeInOut",
        }}
      >
        {/* Core gradient orb */}
        <motion.div
          className="absolute inset-0 rounded-full opacity-80"
          style={{
            background: `radial-gradient(circle at 30% 30%, ${color}, ${secondaryColor} 60%, transparent 80%)`,
            filter: "blur(40px)",
          }}
          animate={{
            opacity: [0.6, 0.9, 0.6],
            filter: [
              "blur(40px)",
              `blur(${40 + audioLevel * 20}px)`,
              "blur(40px)",
            ],
          }}
          transition={{
            duration: 3 / speed,
            repeat: Infinity,
            ease: "easeInOut",
          }}
        />

        {/* Inner glow */}
        <motion.div
          className="absolute inset-4 rounded-full"
          style={{
            background: `radial-gradient(circle at 50% 50%, ${color} 0%, transparent 70%)`,
            opacity: 0.4,
          }}
          animate={{
            opacity: [0.3, 0.5, 0.3],
          }}
          transition={{
            duration: 2 / speed,
            repeat: Infinity,
            ease: "easeInOut",
          }}
        />

        {/* Animated rings */}
        <motion.div
          className="absolute inset-[-20%] rounded-full border border-current opacity-20"
          style={{ color }}
          animate={{
            scale: [1, 1.2, 1],
            opacity: [0.2, 0, 0.2],
          }}
          transition={{
            duration: 4 / speed,
            repeat: Infinity,
            ease: "easeInOut",
          }}
        />
        <motion.div
          className="absolute inset-[-10%] rounded-full border border-current opacity-30"
          style={{ color: secondaryColor }}
          animate={{
            scale: [1.3, 1, 1.3],
            opacity: [0.1, 0, 0.1],
          }}
          transition={{
            duration: 5 / speed,
            repeat: Infinity,
            ease: "easeInOut",
            delay: 0.5,
          }}
        />
      </motion.div>
    </div>
  );
}

/**
 * Orb wrapper with preset colors for different agent states
 */
export function AgentOrb({
  state = "idle",
  size = 120,
  className = "",
}: {
  /** Current agent state */
  state?: "idle" | "listening" | "speaking" | "thinking";
  size?: number;
  className?: string;
}) {
  const config = {
    idle: { color: "#232323", secondaryColor: "#4a4a4a", speed: 0.5, audioReactive: false },
    listening: { color: "#3b82f6", secondaryColor: "#60a5fa", speed: 2, audioReactive: true },
    speaking: { color: "#22c55e", secondaryColor: "#4ade80", speed: 3, audioReactive: true },
    thinking: { color: "#a855f7", secondaryColor: "#c084fc", speed: 1.5, audioReactive: false },
  };

  const { color, secondaryColor, speed, audioReactive } = config[state];

  return (
    <Orb
      size={size}
      color={color}
      secondaryColor={secondaryColor}
      speed={speed}
      audioReactive={audioReactive}
      className={className}
    />
  );
}
